import wx from 'weixin-js-sdk'
import empty from '../components/empty'
import { mapGetters,mapActions } from 'vuex'
import dJq from '../components/djq'
  export default {
    name: "my-cards",
    created(){
      //localStorage.setItem('token','Bearer eyJhbGciOiJIUzUxMiJ9.eyJzdWIiOiIxNzc1NTc1NTY3NiIsInNjb3BlcyI6WyJVU0VSIl0sImlzcyI6Imh0dHA6Ly95dWFvcS5jb20iLCJpYXQiOjE1MjY4ODcwMzksImV4cCI6MTU0NDg4NzAzOX0.JP90fTfrOmDqlVHHyTKJg8WP9TB-O-wdTXX6Xd41xkFJhYLguQf3jUVcY4cvc_d5O0nCBf3laQLtS0AOWjcl8w')
      if(this.$route.query.token){
        localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
      }
      if(this.$route.query.refreshToken){
        localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
      }
      //localStorage.setItem('refreshToken','Bearer eyJhbGciOiJIUzUxMiJ9.eyJzdWIiOiIxODkxMzM1MTg5MSIsInNjb3BlcyI6WyJST0xFX1JFRlJFU0hfVE9LRU4iXSwiaXNzIjoiaHR0cDovL3l1YW9xLmNvbSIsImp0aSI6IjNmMTEyYWJhLTYxNzktNDFhZS1iNjEzLWQwMWEwOGM1NGQ1ZSIsImlhdCI6MTUyNTY4NjY5NCwiZXhwIjoxNTI2MjkxNDk0fQ.uXBS7djRZoCtMr2Pht1TvLfnCTb954OHQwzxdg7BCG8OoKQJ9jhyGJdqyO2lM3cGjS8X0pE5OpDscjFkX5UBmg')
      this.getcoupons({data:{status:'G'},this:this,method:this.getcoupons})
    },
    computed:{
      ...mapGetters({
        dajList:'getdaijinquan'
      })
    },
    methods:{
      ...mapActions(['getcoupons']),
      goToIndex(){
        wx.miniProgram.switchTab({
          url: '/pages/home/index'
        })
      },
      lisy(s){
        if(s=='S'){//自营
          wx.miniProgram.switchTab({
            url: '/pages/home/index'
          })
        }
       else if(s=='A'){
          wx.miniProgram.redirectTo({
            url: '/pages/storeReco/index'
          })
        }
        else{
          wx.miniProgram.switchTab({
            url: '/pages/home/index'
          })
        }
      },
      gotod(obj){
        //this.$router.push({name:'voutails',query:{id:id,s:1}})
        if(obj.type == 'coupon'){
          // wx.miniProgram.navigateTo({
          //   url:'/pages/webview/index?path=voutails&orderId='+obj.productPromoCodeId+'&csi=1'
          // })
          this.$router.push({name:'voutails',query:{id:obj.productPromoCodeId,csi:1}})
        }
        else if(obj.type == 'ticket'){
          // wx.miniProgram.navigateTo({
          //   url:'/pages/webview/index?path=voutails&csi=1&cs='+ encodeURIComponent(JSON.stringify(obj))
          // })
          this.$router.push({name:'voutails',query:{cs:encodeURIComponent(JSON.stringify(obj)),csi:1}})
        }
      }
    },
    components:{
        dJq , empty
    }
  }



